.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
  z-index: 40;
  transition: opacity .25s ease 0s, visibility linear .35s;
  visibility: visible;
  transition-delay: 0s;
  transition-duration: .2s, 0s;
  display: none;
}
@media only screen and (min-width: 48rem) {
  .overlay {
    display: block;
    background-color: #0a1821;
    opacity: .7;
  }
}
.container {
  position: fixed;
  background-color: #fff;
  top: 0;
  left: 0;
  margin: 0;
  z-index: 50;
  text-align: center;
  visibility: visible;
  width: 100vw;
  height: 100vh;
  margin-right: 0rem !important;
  margin-left: 0rem !important;
  overflow-y: scroll;
}
@media only screen and (min-width: 48rem) {
  .container {
    justify-content: center;
    align-items: center;
    width: auto;
    height: auto;
    margin: auto;
    right: 0;
    bottom: 0;
    margin-right: -0.5rem !important;
    margin-left: -0.5rem !important;
    background-color: transparent;
  }
}
.header {
  height: 60px;
  width: 100vw;
  background-color: #fff;
  z-index: 1;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}
.modal {
  text-align: left;
  background-color: #fff;
}
.modalInset {
  background-color: #fff;
  backface-visibility: hidden;
  transform: translate3d(0, 0, 0);
  transform-style: preserve-3d;
  animation: modalComeIn .25s ease;
  visibility: visible;
  /* to keep @ final state */
  overflow-y: scroll;
  height: 100%;
}
@media only screen and (min-width: 48rem) {
  .modalInset {
    box-shadow: 2px 2px 8px 1px rgba(0, 0, 0, 0.2);
    position: relative;
    height: auto;
    overflow-y: auto;
  }
}
.closeWrapper {
  height: 100%;
  position: relative;
}
.close {
  cursor: pointer;
  z-index: 200;
  position: absolute;
  top: 21px;
  left: 10px;
}
.close:hover {
  opacity: 1;
}
@media only screen and (min-width: 64rem) {
  .close {
    opacity: .4;
  }
}
.spacer {
  margin-bottom: 15px;
}
.modalBody {
  transform: translateY(0) scale(0.8, 0.8);
  transition-property: opacity, transform;
  transition-duration: .25s;
  transition-delay: .1s;
  opacity: 1;
  transform: translateY(0) scale(1, 1);
}
.content {
  padding: 16px;
  padding-left: 16px !important;
  padding-right: 16px !important;
}
@keyframes modalComeIn {
  0% {
    visibility: hidden;
    opacity: 0;
    transform: scale(0.8, 0.8);
  }
  65.5% {
    transform: scale(1.03, 1.03);
  }
  100% {
    visibility: visible;
    opacity: 1;
    transform: scale(1, 1);
  }
}
@keyframes modalHeadOut {
  0% {
    visibility: visible;
    opacity: 1;
    transform: translateY(0) scale(1, 1);
  }
  100% {
    visibility: hidden;
    opacity: 0;
    transform: translateY(35px) scale(0.97, 0.97);
  }
}



