.header {
  font-family: 'Roboto Condensed', sans-serif;
  margin-top: 50px;
  margin-bottom: 10px;
  text-transform: uppercase;
  font-size: 3rem;
  line-height: 1;
  letter-spacing: 2px;
  text-align: center;
  font-weight: normal;
}
@media only screen and (min-width: 48rem) {
  .header {
    font-size: 5rem;
    line-height: 5rem;
    letter-spacing: 6px;
  }
}
@media only screen and (min-width: 64rem) {
  .header {
    line-height: 6rem;
    font-size: 6rem;
  }
}
.largeText {
  font-family: 'Roboto Condensed', sans-serif;
  line-height: 1;
  font-size: 3em;
  letter-spacing: .03em;
  font-weight: normal;
  text-transform: uppercase;
  font-weight: 700;
}
@media only screen and (min-width: 64rem) {
  .largeText {
    font-size: 9em;
  }
}
.mediumText {
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 1.5em;
  line-height: 1;
  text-decoration: none;
  letter-spacing: .05em;
}
@media only screen and (min-width: 64rem) {
  .mediumText {
    font-size: 2.5em;
  }
}
.mediumTextBold {
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 1.5em;
  font-weight: bolder;
  line-height: 1;
  text-decoration: none;
  letter-spacing: .05em;
}
@media only screen and (min-width: 64rem) {
  .mediumTextBold {
    font-size: 2.5em;
  }
}
.mediumModalBold {
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 1.5em;
  font-weight: bolder;
  line-height: 1;
  text-decoration: none;
  letter-spacing: .05em;
}
@media only screen and (min-width: 64rem) {
  .mediumModalBold {
    font-size: 2em;
  }
}
.smallText {
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: lighter;
  line-height: 15px;
  letter-spacing: 0.1em;
  font-size: .7em;
}
@media only screen and (min-width: 64rem) {
  .smallText {
    font-size: .8em;
  }
}
.smallTextBold {
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: bolder;
  font-size: .8em;
  line-height: 1;
  letter-spacing: 0.1em;
}
.smallParagraph {
  font-family: 'Roboto Condensed', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: .8rem;
  letter-spacing: 1px;
}
@media only screen and (min-width: 64rem) {
  .smallParagraph {
    font-size: 1em;
  }
}
.code {
  font-family: 'Roboto Mono', monospace;
  font-size: .8em;
  letter-spacing: .1em;
  line-height: 23px;
  color: #4DFF00;
}
.boxTitle {
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 1em;
  letter-spacing: .1em;
  line-height: 23px;
  text-transform: uppercase;
}
.toUpper {
  text-transform: uppercase;
}
.center {
  text-align: center;
}



