.wrap {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0px;
  left: 0px;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}
.comp {
  width: 100%;
  height: 194px;
  text-align: center;
}
.monitor {
  position: relative;
  box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.3);
  width: 275px;
  height: 181px;
  display: block;
  margin: 0 auto;
  border-radius: 10px 10px 0px 0px;
  padding: 9px;
  border: solid 1px #e0e2e2;
  border-bottom: none;
  background-color: #000;
}
.monitor:after {
  position: absolute;
  content: ' ';
  top: -2px;
  left: -2px;
  width: 275px;
  height: 181px;
  border-radius: 10px 10px 0px 0px;
  border: solid 1px #0a1821;
  opacity: .5;
  padding: 10px 10px 8px 10px;
}
.mid {
  display: block;
  height: 100%;
  position: relative;
  background-color: #043E63;
  width: 100%;
}
.base {
  box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.3);
  width: 338px;
  height: 12px;
  background: #e0e2e2;
  display: block;
  margin: 0 auto;
  border-radius: 0px 0px 6px 6px;
}



