.container {
  overflow: hidden;
  color: #0a1821;
  z-index: 100;
}
@media only screen and (min-width: 64rem) {
  .container {
    height: 100vh;
  }
}
.skipWrapper {
  color: #0a1821;
  cursor: pointer;
  text-align: center;
  height: 40px;
  position: fixed;
  top: 350px;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  transition: all .2s ease;
  opacity: .5;
}
.skipWrapper:hover {
  opacity: 1;
}
.skip {
  margin-left: 5px;
}
.enter {
  opacity: 0.01;
  transition: all 0.5s ease-in;
}
.enter.enterActive {
  opacity: 1;
}
.leave {
  opacity: 1;
  transition: all 0.5s ease-in;
}
.leave.leaveActive {
  opacity: 0.01;
}
.appear {
  opacity: 0.01;
}
.appear.appearActive {
  opacity: 1;
  transition: all 0.5s ease-in;
}



