.nav {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-top: 1px solid #0a1821;
}
@media only screen and (min-width: 64rem) {
  .nav {
    display: block;
    border-top: none;
    text-align: center;
    height: 225px;
  }
}
.navIcon {
  color: #0a1821;
  font-size: 2em;
  transition: all 0.3s ease-out;
  opacity: 0;
}
@media only screen and (min-width: 64rem) {
  .navIcon {
    opacity: 1;
  }
}
.link {
  background-color: transparent;
  font-size: 24px;
  color: #4d4d4e;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  line-height: 45px;
  height: 45px;
  width: 100%;
  position: relative;
}
.link:after {
  content: '';
  font-size: 12px;
  position: absolute;
  left: 0;
  bottom: 0;
  display: block;
  width: 100%;
  height: 45px;
  text-align: center;
  opacity: 1;
  transition: all 0.3s ease-out;
}
.link:hover:after {
  opacity: 1;
  color: #FF8E00;
}
.link:hover .navIcon {
  opacity: 0;
}
@media only screen and (min-width: 64rem) {
  .link:after {
    opacity: 0;
    color: #FF8E00;
  }
  .link .navIcon {
    opacity: 1;
  }
}
.home:after {
  content: 'Home';
}
.male:after {
  content: 'About';
}
.code:after {
  content: 'Portfolio';
}
.blog:after {
  content: 'Blog';
}
.enter {
  opacity: 0.01;
  transition: opacity 0.5s ease-in;
}
.enter.enterActive {
  opacity: 1;
}
.leave {
  opacity: 1;
  transition: opacity 0.5s ease-in;
}
.leave.leaveActive {
  opacity: 0.01;
}
.appear {
  opacity: 0.01;
}
.appear.appearActive {
  opacity: 1;
  transition: opacity 0.5s ease-in;
}
.selected:after {
  opacity: 1;
  color: #FF8E00;
}
.selected .navIcon {
  opacity: 0;
}



