.fourThree {
  position: relative;
}
.fourThree:before {
  display: block;
  content: "";
  width: 100%;
  padding-top: 75%;
}
.fourThree > .content {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.fourThree:hover .overlay {
  opacity: 1;
}
.container {
  position: relative;
  padding-right: 0rem !important;
  padding-left: 0rem !important;
}
.container:before {
  content: " " !important;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  pointer-events: none;
  z-index: 3;
  transition: all .3s ease;
  opacity: 0;
}
.container span {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.overlay {
  top: 0;
  z-index: 1;
  color: rgba(255, 255, 255, 0.3);
  font-size: 12px;
  width: 50px;
  pointer-events: none;
  -webkit-font-smoothing: antialiased;
  padding: 0;
  position: absolute;
  height: 100%;
  opacity: 0;
  transition: all .3s ease;
}
.overlayLeft {
  background: linear-gradient(to left, transparent, rgba(0, 0, 0, 0.3));
  left: 0;
}
.overlayRight {
  background: linear-gradient(to right, transparent, rgba(0, 0, 0, 0.3));
  right: 0;
}
.arrow {
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto 0;
  background-position: center;
  background-repeat: no-repeat;
  height: 40px;
  width: 40px;
  z-index: 100;
  cursor: pointer;
  transition: all .3s ease;
  transform: translateZ(0);
  opacity: .5;
}
.arrow:hover {
  opacity: .8;
}
.leftArrow {
  background-image: url('../../base/assets/leftArrow.svg');
  left: 0px;
}
.rightArrow {
  right: 0px;
  background-image: url('../../base/assets/rightArrow.svg');
}
.img {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}
.enter {
  opacity: 0.01;
  transition: all 0.5s ease-in;
}
.enter.enterActive {
  opacity: 1;
}
.leave {
  opacity: 1;
  transition: all 0.5s ease-in;
}
.leave.leaveActive {
  opacity: 0.01;
}
.appear {
  opacity: 0.01;
}
.appear.appearActive {
  opacity: 1;
  transition: all 0.5s ease-in;
}



