.header {
  width: 100vw;
  position: fixed;
  background-color: #fff;
  top: 0;
  left: 0;
  z-index: 3;
  min-height: 0;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}
@media only screen and (min-width: 64rem) {
  .header {
    display: flex;
    box-shadow: none;
    flex-direction: column;
    justify-content: space-between;
    width: 60px;
    height: 100vh;
  }
}
.loading {
  z-index: -1;
}
.basicNav {
  height: 60px;
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}
.mobileBars {
  font-size: 30px !important;
  color: #0a1821;
  margin-right: 10px;
}
@media only screen and (min-width: 64rem) {
  .mobileBars {
    display: none !important;
  }
}
.logo {
  margin: 15px 0 0 6px;
  background-image: url('../base/assets/spaceNeedle.svg');
  background-position: center;
  background-repeat: no-repeat;
  height: 60px;
  width: 50px;
  text-decoration: none;
  background-size: contain;
}
.icons {
  font-size: 20px !important;
  color: #0a1821;
  margin-bottom: 10px;
}
.social {
  display: none;
}
@media only screen and (min-width: 64rem) {
  .social {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}



