.container {
  cursor: pointer;
  color: #0a1821;
  position: relative;
  margin: .5rem 0 1rem 0;
  transition: box-shadow 0.25s;
  border-radius: 2px;
  background-color: #fff;
  transform: scale(1);
  transition: all .3s ease;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.1), 0 1px 5px 0 rgba(0, 0, 0, 0.1), 0 3px 1px -2px rgba(0, 0, 0, 0.1);
}
.container:hover {
  transform: translate3d(0, 0, 0);
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}
.container:hover .imgContainer:before {
  opacity: 1;
}
.container:hover .cardTitle {
  transform: translateY(0%);
}
.imgContainer {
  position: relative;
  transition: all .5s ease;
  overflow: hidden;
}
.imgContainer:before {
  transition: all .5s ease;
  background: rgba(0, 0, 0, 0.6);
  content: " " !important;
  height: 100%;
  left: 0;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 3;
}
.cardImg {
  transform: scale(1);
  transition: all .3s ease;
  display: block;
  border-radius: 2px 2px 0 0;
  position: relative;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
}
.cardTitle {
  color: #fff;
  text-align: left;
  position: absolute;
  bottom: 0;
  transform: translateY(70%);
  left: 0;
  max-width: 100%;
  padding: 24px;
  z-index: 4;
  transition-property: all;
  transition-duration: .3s;
  transition-timing-function: ease;
  transition-delay: .2s;
}
.cardContent {
  display: flex;
  padding: 24px;
  border-radius: 0 0 2px 2px;
  text-align: left;
}
.cardAction {
  text-align: left;
  position: relative;
  background-color: inherit;
  padding: 16px 24px;
  border-radius: 0 0 2px 2px;
  z-index: 100;
}
.cardAction a {
  color: #0a1821;
}
.stack,
.wrench {
  color: #0a1821;
}
.stack {
  line-height: 1.4;
  max-height: 4.2em;
  line-clamp: 3;
  overflow: hidden;
  text-overflow: ellipsis;
}
.wrench {
  margin-right: 5px;
}



