html {
  height: 100%;
  width: 100%;
  font-family: 'Roboto Condensed', sans-serif;
  box-sizing: border-box;
  font-smoothing: antialiased;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  font-size: 16px;
}
body {
  height: 100%;
  width: 100%;
  background: #fff;
}
a {
  text-decoration: none;
  color: #074F7E;
  cursor: pointer;
}
html,
body,
main,
h1,
h2,
h3,
p {
  margin: 0;
  padding: 0;
}
.mainWrapper {
  flex-direction: column;
  justify-content: center;
  margin-left: 0px !important;
  margin-right: 0px !important;
}
.content {
  background: transparent;
  z-index: 200;
  padding-top: 60px;
  margin: auto;
}
@media only screen and (min-width: 64rem) {
  .content {
    padding-top: 0px;
    padding-left: calc(60px + 0.5rem) !important;
    width: calc(100% - 60px);
  }
}
.lines {
  position: fixed;
  top: 60px;
  left: 8%;
  width: 100vw;
  height: calc(100vh - 60px);
  pointer-events: none;
  margin-left: 0px !important;
  margin-right: 0px !important;
}
@media only screen and (min-width: 64rem) {
  .lines {
    top: 0px;
    left: 60px;
    height: 100vh;
    width: calc(100% - 60px);
  }
}
.lineRow {
  height: 100%;
}
.line {
  width: 1px;
  position: relative;
  background-image: radial-gradient(circle, rgba(10, 24, 33, 0.1), #fff);
}
.line:before {
  position: absolute;
  left: -1px;
  content: ' ';
  height: 3px;
  width: 3px;
  border-radius: 50%;
  animation-name: dot;
  animation-duration: 3s;
  animation-timing-function: cubic-bezier(0.3, 1, 0.9, 0.2);
  animation-direction: alternate;
  animation-iteration-count: infinite;
  animation-fill-mode: none;
  animation-play-state: running;
  transform: translate3d(0, 0, 0);
  opacity: 1;
}
@keyframes dot {
  0% {
    transform: translateY(0vh);
  }
  100% {
    transform: translateY(100vh);
  }
}
.line:nth-child(1) {
  display: none;
}
@media only screen and (min-width: 64rem) {
  .line:nth-child(1) {
    display: block;
  }
  .line:nth-child(1):before {
    animation-delay: 0s;
    background: #1921B1;
  }
}
.line:nth-child(2):before {
  animation-delay: .5s;
  background: #00B25C;
}
.line:nth-child(3) {
  display: none;
}
@media only screen and (min-width: 64rem) {
  .line:nth-child(3) {
    display: block;
  }
  .line:nth-child(3):before {
    animation-delay: 1s;
    background: #FF8E00;
  }
}
.line:nth-child(4):before {
  animation-delay: 1.5s;
  background: #1921B1;
}
.line:nth-child(5) {
  display: none;
}
@media only screen and (min-width: 64rem) {
  .line:nth-child(5) {
    display: block;
  }
  .line:nth-child(5):before {
    animation-delay: 2s;
    background: #00B25C;
  }
}
.line:nth-child(6):before {
  animation-delay: 2.5s;
  background: #FF8E00;
}
.line:nth-child(7):before {
  animation-delay: 3s;
  background: #1921B1;
}



