.container {
  justify-content: center;
  position: relative;
}
.listContainer {
  background: transparent;
  color: #fff;
  padding: 50px 0;
}
.listItem {
  list-style-type: none;
  position: relative;
  margin: 0 auto;
  padding-top: 50px;
  background: transparent;
}
.listItem:after {
  content: ' ';
  position: absolute;
  left: -9%;
  bottom: 5px;
  transition: all .3s ease;
  transform: translateX(-50%);
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #00B25C;
  opacity: 1;
}
@media only screen and (min-width: 48rem) {
  .listItem:after {
    bottom: 0;
    left: -15%;
    width: 30px;
    height: 30px;
  }
}
.listItem > .itemContent::before {
  left: -16px;
  border-width: 8px 16px 8px 0;
  border-color: transparent #006C38 transparent transparent;
}
@media only screen and (min-width: 48rem) {
  .listItem {
    margin: 0 auto;
    width: 6px;
  }
  .listItem:after {
    left: 50%;
  }
  .listItem:nth-child(odd) .itemContent {
    left: 45px;
  }
  .listItem:nth-child(odd) .itemContent::before {
    left: -16px;
    border-width: 8px 16px 8px 0;
    border-color: transparent #1921B1 transparent transparent;
  }
  .listItem:nth-child(even) .itemContent {
    left: -439px;
  }
  .listItem:nth-child(even) .itemContent::before {
    right: -16px;
    left: auto;
    border-width: 8px 0 8px 16px;
    border-color: transparent transparent transparent #1921B1;
  }
}
.itemContent {
  position: relative;
  bottom: 0;
  background: #fff;
  box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.3);
  opacity: 0;
}
.itemContent:before {
  content: '';
  position: absolute;
  bottom: 7px;
  width: 0;
  height: 0;
  border-style: solid;
}
@media only screen and (min-width: 48rem) {
  .itemContent {
    width: 400px;
  }
}
.inView {
  animation-name: inView;
  animation-duration: .3s;
  animation-delay: .3s;
  animation-timing-function: cubic-bezier(0.23, 1, 0.32, 1);
  animation-fill-mode: forwards;
}
.inView:after {
  opacity: 1;
}
@keyframes inView {
  0% {
    opacity: 0;
    transform: translate3d(0px, 200px, 0);
  }
  100% {
    opacity: 1;
    transform: none !important;
  }
}
.title {
  background: #fff;
  color: #0a1821;
  padding: 15px;
}
.date {
  color: #0a1821;
  padding: 15px;
}
.summary {
  color: #0a1821;
  padding: 15px;
  line-height: 2rem;
  opacity: .7;
}



